<template>
  <img aly="Vue logo" src="./assets/img.png" />
  <br>
  <button v-on:click="showData">คลิกเพื่อดูชื่อ</button>
  <button @click="showData">คลิกเพื่อดูนามสกุล</button>
  <h1>{{firstname}} {{lastname}}</h1>
  <h3>{{age}}</h3>
  <button @click="plus">เพิ่มอายุ</button>
  <button @click="plos">ลดอายุ</button>
  <br>
  <button @click="pluss(5)">เพิ่ม+5</button>
  <button @click="ploss(10)">เพิ่มอายุ+10</button>
  <br>
  <form>
        <label for="">ป้อนชื่อเล่น</label>
    <input type="text" v-on:input="setnickname">
    <br>
        <label for="">ป้อนวัน/เดือน/ปีเกิด</label>
    <input type="text" v-on:input="setday">
    <br>
        <label for="">ป้อนสาขา</label>
    <input type="text" v-on:input="setsaka">
    <br>
        <label for="">ป้อนห้อง</label>
    <input type="text" v-on:input="setroom">
    <br>
        <label for="">ป้อนคติประจำใจ</label>
    <input type="text" v-on:input="setkati">
    <br>
        <label for="">ป้อนสถานที่เรียนต่อ</label>
    <input type="text" v-on:input="setlern">
    <br>
        <label for="">ป้อนวิชาที่ไม่ชอบ</label>
    <input type="text" v-on:input="setunlike">
    <br>
    
    
  </form>
  <h2>แสดงชื่อจริง : {{firstname}}</h2>
  <h2>แสดงนามสกุล : {{lastname}}</h2>
  <h2>แสดงชื่อเล่น : {{nickname}}</h2>
  <h2>แสดงอายุ : {{age}}</h2>
  <h2>แสดงวัน/เดือน/ปีเกิด : {{day}}</h2>
  <h2>แสดงสาขา : {{saka}}</h2>
  <h2>แสดงห้อง : {{room}}</h2>
  <h2>แสดงคติประจำใจ : {{kati}}</h2>
  <h2>แสดงสถานที่เรียนต่อ : {{lern}}</h2>
  <h2>แสดงวิชาที่ไม่ชอบ : {{unlike}}</h2>


</template>

<script>
export default {
  name: 'App',
  data(){
    return{
      firstname : "Thirachot",
      lastname : "Nontnong",
      age : 20,
      nickname : "",
      day : "",
      saka : "",
      room : "",
      kati : "",
      lern : "",
      unlike : "",
    }
  },
  methods:{
    showData(){
        alert(this.firstname+this.lastname)
    },
    plus(){
      this.age++
    },
    pluss(val){
    this.age+=val
    },
    plos(){
      this.age--
    },
    ploss(val){
    this.age-=val
    },
    setnickname(event){
      //console.log(event.target.value)
      this.nickname = event.target.value
    },
    setday(event){
      //console.log(event.target.value)
      this.day = event.target.value
    },
    setsaka(event){
      this.saka = event.target.value
    },
    setroom(event){
      this.room = event.target.value
    },
    setkati(event){
      this.kati = event.target.value
    },
    setlern(event){
      this.lern = event.target.value
    },
    setunlike(event){
      this.unlike = event.target.value
    },
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>